<template>
  <app-content-wrapper :pageTitle="pageTitle">
    <ui-loading :activeModel="!previewLoaded" :fullPage="true"></ui-loading>

    <template v-if="!dataLoaded">
      <ui-skeletor :height="'1.3rem'"></ui-skeletor>
      <ui-skeletor :height="'1.3rem'"></ui-skeletor>
      <ui-skeletor :height="'1.3rem'"></ui-skeletor>
    </template>

    <div class="write__wrapper" v-else>
      <ui-form :name="'savePostForm'"
               :ref="'savePostForm'"
               :class="'write__frm'"
               :preventEnter="true"
               @onsubmit="onSubmit"
      >
        <div class="write__save-wrapper">
          <ui-button :type="'button'"
                     :color="'primary'"
                     :class="'write__btn--load'">불러오기
          </ui-button>
    
          <ul class="write__save-list"></ul>
        </div>

        <ui-hidden-field :name="'id'" :id="'savePostId'" :value="post?.id"></ui-hidden-field>
    
        <ui-write-table :name="'포스트 작성 폼'">
          <tr>
            <th scope="row">
              <label for="savePostTitle">제목</label>
            </th>
            <td>
              <div class="write__title">
                <ui-select :name="'categoryId'"
                           :id="'savePostCategory'"
                           :class="'write__select-category'"
                           :title="'카테고리 선택'"
                           :defaultValue="'카테고리 선택'"
                           :rules="'required'"
                           :data="categoryList"
                           v-model="categoryId">
                </ui-select>
  
                <ui-text-field :type="'text'"
                               :name="'title'"
                               :id="'savePostTitle'"
                               :rules="'required'"
                               :block="true"
                               :value="post?.title">
                </ui-text-field>
              </div>
            </td>
          </tr>
          <tr>
            <th scope="row">
              <label for="savePostCont">내용</label>
            </th>
            <td>
              <ui-textarea :name="'cont'"
                           :id="'savePostCont'"
                           :cols="'80'"
                           :rows="'18'"
                           :rules="'required'"
                           :hidden="true"
                           v-model="cont">
              </ui-textarea>

              <md-editor :language="'en-US'"
                         :preview="false"
                         v-model="cont">
              </md-editor>
            </td>
          </tr>
          <tr>
            <th scope="row">
              <label for="savePostOgDesc">본문 요약</label>

              <ui-icon-button :type="'button'"
                              :icon="'xi-refresh'"
                              :text="'본문 요약 갱신'"
                              :class="'write__og-desc-refresh'"
                              @click="changeOgDesc">
              </ui-icon-button>
            </th>
            <td>
              <ui-text-field :type="'text'"
                             :name="'ogDesc'"
                             :id="'savePostOgDesc'"
                             :class="'write__og-desc'"
                             :placeholder="'50자 이내(생략 시, 제목이 들어감)'"
                             :block="true"
                             :value="post?.ogDesc">
              </ui-text-field>
            </td>
          </tr>
          <tr>
            <th scope="row">
              <label for="savePostOgImgFile">대표 이미지</label>
            </th>
            <td>
              <ui-hidden-field :name="'ogImg'" :id="'savePostOgImg'" :value="post?.avatarImg"></ui-hidden-field>
              <ui-hidden-field :name="'ogImgUrl'" :id="'savePostOgImgUrl'" :value="post?.avatarImgUrl"></ui-hidden-field>
              <ui-hidden-field :name="'ogImgSize'" :id="'savePostOgImgSize'" :value="post?.avatarImgSize"></ui-hidden-field>

              <ui-file-field :name="'ogImgFile'"
                             :id="'savePostOgImgFile'"
                             :accept="'image/*'"
                             :gap="10"
                             @onchange="onChangeOgImg">
                
                <ui-file-button :type="'button'"
                                :color="'secondary'"
                                @clickFile="onClickFile">Cloudinary
                </ui-file-button>
              </ui-file-field>

              <ui-file-info :imgName="ogImg"
                            :imgSize="ogImgSize"
                            v-if="post?.ogImg"
              >
                <ui-checkbox :name="'delOgImg'"
                             :id="'savePostDelOgImg'"
                             :class="'ml--10'"
                             :label="'삭제'"
                             :values="'Y,N'">
                </ui-checkbox>
              </ui-file-info>
            </td>
          </tr>
          <tr v-if="'D01002' === $route.meta.type">
            <th scope="row">
              <label for="savePostAddTag">태그</label>
            </th>
            <td>
              <ui-hidden-field :name="'saveTagList'" :id="'saveTagList'"></ui-hidden-field>
            
              <div class="write__tag__wrapper">
                <ui-text-field :type="'text'"
                               :name="'addTag'"
                               :id="'savePostAddTag'"
                               :class="'write__tag'"
                               :placeholder="'태그 입력 (쉼표로 구분, 5개까지 입력 가능)'"
                               :block="true"
                               v-model="tagStr">
                </ui-text-field>

                <ui-select :name="'tag'"
                           :id="'savePostTag'"
                           :class="'write__select-tag'"
                           :title="'태그 선택'"
                           :defaultValue="'나의 태그'"
                           :data="tagList"
                           @onchange="onChangeTag">
                </ui-select>
              </div>
            </td>
          </tr>
          <tr v-if="'D01002' === $route.meta.type">
            <th scope="row">공개/비공개</th>
            <td>
              <ui-radio :id="'savePostSecretN'"
                        :name="'secretYn'"
                        :label="'공개'"
                        :rules="'required'"
                        :value="'N'"
                        v-model="secretYn">
              </ui-radio>
          
              <ui-radio :id="'savePostSecretY'"
                        :name="'secretYn'"
                        :label="'비공개'"
                        :rules="'required'"
                        :value="'Y'"
                        v-model="secretYn">
              </ui-radio>
            </td>
          </tr>
          <tr v-if="'D01002' === $route.meta.type">
            <th scope="row">상단고정</th>
            <td>
              <ui-radio :id="'savePostPinN'"
                        :name="'pinYn'"
                        :label="'비고정'"
                        :rules="'required'"
                        :value="'N'"
                        v-model="pinYn">
              </ui-radio>

              <ui-radio :id="'savePostPinY'"
                        :name="'pinYn'"
                        :label="'고정'"
                        :rules="'required'"
                        :value="'Y'"
                        v-model="pinYn">
              </ui-radio>
            </td>
          </tr>

          <template v-slot:btn>
            <ui-button :type="'button'"
                       :color="'warning'"
                       :class="'write__btn'"
                       @click="previewPost">미리보기
            </ui-button>
            
            <ui-button :type="'button'"
                       :color="'success'"
                       :class="'write__btn'">임시저장
            </ui-button>

            <ui-button :type="'reset'"
                       :color="'secondary'"
                       :class="'write__btn'">다시작성
            </ui-button>

            <ui-button :type="'submit'"
                       :color="'primary'"
                       :class="'write__btn'">저장
            </ui-button>
          </template>
        </ui-write-table>
      </ui-form>
    </div>
  </app-content-wrapper>
</template>

<script>
import { arrayHasDuplicateValue, getFormValues, isBlank, isNotEmpty, messageUtil } from '@/utils';
import { breadcrumbService } from '@/services/breadcrumb/breadcrumbService';
import AppSavePostTag from '@/components/views/post/AppSavePostTag.vue';
import AppPreviewPostModal from '@/components/views/post/AppPreviewPostModal.vue';

import MdEditor from 'md-editor-v3';
import 'md-editor-v3/lib/style.css';

export default {
  name: 'app-add-post',
  components: {
    AppSavePostTag,
    MdEditor,
  },
  data() {
    return {
      /** 페이지 타이틀 */
      pageTitle: '',
      /** 수정 포스트 */
      post: null,
      /** 수정 포스트 내용 */
      cont: '',
      /** 대표 이미지 */
      ogImg: '',
      /** 대표 이미지 용량 */
      ogImgSize: '',
      /** 대표 이미지 목록 */
      ogImgFileList: [],
      /** 포스트 카테고리 목록 */
      categoryList: [],
      /** 수정 포스트의 카테고리 ID */
      categoryId: '',
      /** 포스트 태그 목록 */
      tagList: [],
      /** 포스트 비공개 여부 */
      secretYn: 'N',
      /** 포스트 상단고정 여부 */
      pinYn: 'N',
      /** 포스트 작성/수정 시 입력한 태그 문자열 */
      tagStr: '',
      /** 포스트 작성/수정 시 추가한 태그 목록 */
      saveTagList: [],
      /** 데이타 로딩 완료 여부 */
      dataLoaded: false,
      /** 미리보기 데이타 로딩 완료 여부 */
      previewLoaded: false,
    }
  },
  /** 해당 컴포넌트를 벗어나 새로운 페이지로 이동할 때 호출됨 */
  async beforeRouteLeave(to, from) {
    if ('/a/goto' === to.path || '/' === to.path) return true;

    const confirm = await messageUtil.confirmWarning('이 사이트에서 나가시겠습니까?', '변경한 내용이 저장되지 않을 수 있습니다.');
    if (!confirm) return false;
  },
  created() {
    this.pageTitle = !this.isUpdatePage ? '포스트 작성' : '포스트 수정';
    
    // 페이지 타이틀 세팅
    breadcrumbService.setPageTitle(this.pageTitle);

    this.init();
  },
  computed: {
    /** 포스트 수정 페이지인지 확인 */
    isUpdatePage: {
      get() {
        return isNotEmpty(this.$route.params.id);
      },
      set(v) {}
    }
  },
  methods: {
    /** 초기 세팅 */
    async init() {

      this.previewLoaded = true;

      // 포스트 수정 페이지일 경우, 포스트 조회 메서드를 실행하기 위함
      if (this.isUpdatePage) {
        await Promise.all([
          this.listCategory(),
          this.listTag(),
          this.getPost(this.$route.params.id),
        ]);
      } else {
        this.dataLoaded = true;
        
        await Promise.all([
          this.listCategory(),
          this.listTag(),
        ]);
      }

      this.dataLoading();
    },
    /** 포스트 저장 */
    async onSubmit(values) {
      const isValid = this.validationCheck();
      if (!isValid) return;

      const confirm = await messageUtil.confirmSuccess('저장하시겠습니까?');
      if (!confirm) return;

      // 태그 배열에 추가
      this.setTagArr(values);
      
      const headers = { 'Content-Type': 'multipart/form-data' };

      if (this.isUpdatePage) {
        await this.$http.put('/post', values, { headers });
      } else {
        await this.$http.post('/post', values, { headers });
      }

      messageUtil.toastSuccess('저장되었습니다.');

      this.$store.dispatch('Post/FETCH_MAIN_POSTLIST', {});
      this.$store.dispatch('Layout/FETCH_SIDEBAR', {});

      this.$router.push('/');
    },
    /** 포스트 조회 */
    getPost(id) {
      return this.$http.get(`/post/${id}`)
      .then(res => {
        this.post = { ...res.data };

        this.cont = this.post.rawText;
        this.secretYn = this.post.secretYn;
        this.pinYn = this.post.pinYn;
        this.categoryId = this.post.postCategory[0].categoryId;
        this.ogImg = this.post.ogImg;
        this.ogImgSize = this.post.ogImgSize;
        this.tagStr = this.post.postTag.map(d => d.tag.nm).join(', ');
      });
    },
    /** 본문 요약 버튼 클릭 시 */
    async changeOgDesc() {
      const title = this.$refs['savePostForm'].getFieldValue('title');

      if (isBlank(title)) {
        messageUtil.toastWarning('제목을 입력하세요.');
        return;
      }

      const confirm = await messageUtil.confirmSuccess('본문 요약을 제목과 맞추시겠습니까?');
      if (!confirm) return;

      this.$refs['savePostForm'].setFieldValue('ogDesc', title);
    },
    /** cloudinary 파일 클릭 시 */
    onClickFile(file) {
      this.$refs['savePostForm'].setFieldValue('ogImg', file.public_id + '.' + file.format);
      this.$refs['savePostForm'].setFieldValue('ogImgUrl', file.url);
      this.$refs['savePostForm'].setFieldValue('ogImgSize', file.bytes);
    },
    /** 포스트 대표 이미지 file input 값 변경 시 */
    onChangeOgImg(values) {
      if (isNotEmpty(values)) {
        this.ogImg = values.name;
        this.ogImgSize = values.size;
      }
    },
    /** 카테고리 목록 조회 */
    listCategory() {
      return this.$http.get('/category')
      .then(res => {
        res.data.map(d => {
          this.categoryList.push({
            value: d.id,
            text: d.nm,
          });
        });
      });
    },
    /** 태그 목록 조회 */
    listTag() {
      return this.$http.get('/tag')
      .then(res => {
        res.data.map(d => {
          this.tagList.push({
            value: d.id,
            text: d.nm,
          });
        });
      });
    },
    /** 태그 선택 시 */
    onChangeTag(value) {
      const foundTag = this.tagList.find(d => d.value == value).text;

      if (isBlank(this.tagStr)) {
        this.tagStr = foundTag;
      } else {
        this.tagStr += `, ${foundTag}`;
      }
    },
    /** 태그 배열에 추가 */
    setTagArr(formValues) {
      if (isBlank(this.tagStr)) {
        return;
      }

      this.saveTagList = [];

      const tagArr = this.tagStr.split(',');

      for (let i = 0; i < tagArr.length; i++) {
        const foundTag = this.tagList.find(d => d.text === tagArr[i].trim());

        this.saveTagList.push({
          // DB에서 조회해온 태그와 일치하는 태그가 있을 경우에만 id 키값을 넣는다. 키값이 있으면 UPDATE, 없으면 INSERT를 하기 위함
          ...(isNotEmpty(foundTag) && { id: foundTag.value }),
          nm: tagArr[i].trim(),
        });
      }

      formValues['saveTagList'] = JSON.stringify(this.saveTagList);
    },
    /** 포스트 미리보기 */
    previewPost() {
      const body = getFormValues(this.$refs['savePostForm'].$el);
      
      this.previewLoaded = false;

      return this.$http.post('/post/preview', body)
      .then(res => {
        this.previewLoaded = true;

        this.$modal.show({
          component: AppPreviewPostModal,
          bind: {
            post: res.data,
          },
        });
      });
    },
    /** 유효성 검사 */
    validationCheck() {
      const tagArr = this.tagStr.split(',');

      if (arrayHasDuplicateValue(tagArr)) {
        messageUtil.toastWarning('중복된 태그가 있습니다.');
        return false;
      }

      if (5 < tagArr.length) {
        messageUtil.toastWarning('태그는 5개까지 입력할 수 있습니다.');
        return false;
      }

      return true;
    },
    /** 데이타 로딩 */
    dataLoading() {
      if (isNotEmpty(this.post)) {
        this.dataLoaded = true;
      }
    },
  },
}
</script>

<style lang="scss" scoped>
@import '@/assets/scss/views/write.scss';
</style>